* {
  box-sizing: border-box;
}

#yash_div {
  height: auto;
}

.maindiv_content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
  row-gap: 30px;
  padding: 1.875rem;
  height: 100vh;
}

.offi_websites,
.yt_videos,
.Projects,
.Roadmap,
.Courses,
.Suggestion_form {
  border: 2px solid rgb(0, 110, 125);
  box-shadow: 0 0 2px #37d8db, 0 0 5px #0a8f9e, 0 0 10px #199fae;
  background-color: #314650;
  color: white;
  border-radius: 2.188rem;
  height: 300px;
}

.offi_websites h4,
.yt_videos h4,
.Projects h4,
.Roadmap h4,
.Courses h4,
.Suggestion_form h4 {
  text-align: center;
  margin: auto;
}

#link_con {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  border: 2px solid rgb(95, 224, 231);
  border-radius: 20px;
  height: 85%;
  width: 90%;
  margin: auto;
}

.all_links {
  border: 2px solid rgb(70, 175, 182);
  height: 190px;
  width: 95%;
  margin: auto;
  border-radius: 20px;
}

.modal_button {
  height: 40px;
  width: 95%;
  margin: auto;
  border-radius: 20px;
}

.modal_button button {
  height: 35px;
  width: 100%;
  border-radius: 20px;
  background-color: aqua;
  font-size: 16px;
  font-weight: bold;
  font-style: "Poppins", sans-serif;
}

#close_btn {
  font-size: 16px;
  font-weight: bold;
  font-style: "Poppins", sans-serif;
}

.modal_button button:hover {
  background-color: greenyellow;
  box-shadow: 0 0 2px #37b5db, 0 0 5px #37cbdb, 0 0 10px #37c5db;
}

.site1 {
  margin: auto;
  padding: 10px;
}

.site1 p {
  border: 2px solid rgb(165, 179, 245);
  padding: 5px 15px 5px 15px;
  border-radius: 10px;
  box-shadow: 0 0 2px #37dbdb, 0 0 5px #37c8db, 0 0 10px #37cddb;
}

.img_marquee {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  border: 2px solid rgb(78, 177, 186);
  border-radius: 20px;
  height: 85%;
  width: 90%;
  margin: auto;
}

.yt1 {
  margin: auto;
  padding: 10px;
}

.im1 {
  height: 200px;
  width: 390px;
  justify-content: center;
}

.cr1 {
  margin: auto;
  padding: 10px;
}

.course_marquee {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  border: 2px solid rgb(176, 60, 218);
  border-radius: 20px;
  height: 85%;
  width: 90%;
  margin: auto;
}

.roadmap_con {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  border: 2px solid rgb(95, 136, 231);
  border-radius: 20px;
  height: 85%;
  width: 90%;
  margin: auto;
}

.road_img {
  border: 2px solid rgb(100, 70, 182);
  height: 190px;
  width: 95%;
  margin: auto;
  border-radius: 20px;
}

.road_img img {
  height: 190px;
  width: 70%;
}

.button {
  border: 2px solid rgb(30, 25, 111);
  height: 30px;
  background-color: rgb(200, 186, 237);
  width: 95%;
  margin: auto;
  border-radius: 20px;
}

.button a {
  text-align: center;
  line-height: 30px;
  vertical-align: middle;
  justify-content: flex-start;
  margin-top: 0;
}

.btn {
  border: 2px solid rgb(106, 102, 236);
}

.project_con {
  display: flex;
  flex-direction: column;
  border: 2px solid rgb(29, 18, 111);
  border-radius: 20px;
  height: 85%;
  width: 90%;
  margin: auto;
}

.suggest_con {
