  border-radius: 20px;
}

.texts {
  margin-top: 5rem;
  text-align: center;
  margin-bottom: 5rem;
  border-radius: 18rem;
  border: 2px solid transparent;
  border-color: rgb(51, 70, 155);
  height: 70%;
  width: 80%;
  padding-top: 4rem;
  padding-bottom: 5rem;
  margin-left: 3rem;
  font-size: 25px;
}

@media only screen and (max-width: 550px) {
  #yash_div {
    grid-template-columns: 1fr;
  }
  .swiper {
    width: 450px;
    height: 200px;
    margin: auto;
    border-radius: 20px;
    z-index: -1;
  }
}

.swiper {
  width: 390px;
  height: 220px;
  margin: auto;
  border-radius: 20px;
  z-index: 1;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
